import { Component, Inject, OnInit, Renderer2 } from '@angular/core';
import * as fromHeaderSelectors from 'src/app/store/selectors/header.selectors';
import { Observable, from } from 'rxjs';
import { Store, select } from '@ngrx/store';
import { AppState } from 'src/app/store';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-header',
  template: (require('./header.component.html').default || require('./header.component.html')).length ? (require('./header.component.html').default || require('./header.component.html')) : '',
  styles: [(require('./header.component.scss').default || require('./header.component.scss')).length ? (require('./header.component.scss').default || require('./header.component.scss')) : ''],
})
export class HeaderComponent implements OnInit {
  vm$: Observable<fromHeaderSelectors.HeaderViewModel>;
  darkTheme = new FormControl(false);

  constructor(private store: Store<AppState>,
    // @Inject(DOCUMENT) private document: Document,
    // private renderer: Renderer2
    
    ) {}

  ngOnInit(): void {
    this.initializeTheme();


    this.vm$ = this.store.pipe(
      select(fromHeaderSelectors.selectHeaderViewModel)
    );
    
  }


  switchTheme() {
    this.document.body.classList.replace(
      this.theme,
      this.theme === 'light-theme'
        ? (this.theme = 'dark-theme')
        : (this.theme = 'light-theme')
    );
  }

  initializeTheme = (): void =>
    this.renderer.addClass(this.document.body, this.theme);
}

export type Theme = 'light-theme' | 'dark-theme';
